.video-overlay-play-button {
    box-sizing: border-box;
    border-radius: 20px;
    margin-top: 40px;
    width: 100%;
    height: 80%;
    padding: 10px calc(50% - 50px);
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    opacity: 1;
    cursor: pointer;
    background-image: linear-gradient(#E4E4E4, #E4E4E4);
    
}

.video-overlay-play-button:hover {
    opacity: 1;
}

